import React, { Component } from 'react';
import { Symbol } from "./Symbol";
import { Tr, Td } from 'react-super-responsive-table';
import 'react-super-responsive-table/dist/SuperResponsiveTableStyle.css';


export class Compare extends Component {

  constructor(props) {
    super(props);
    let compare = props.compare;
    let buy = compare.Symbols.find(symbol => symbol.IsBuy === true);
    let sell = compare.Symbols.find(symbol => symbol.IsBuy === false);
    this.state = { compare: props.compare, buy: buy, sell: sell };
    // console.log(props.compare);
  }

  render() {
    var oldDate = this.state.compare.UpdateTime.toString();
    let newDate = new Date().toUTCString();
    let ticks = Date.parse(newDate) - Date.parse(oldDate);
    let updateTime = new Date(ticks).toLocaleTimeString([], { minute: "2-digit", second: "2-digit" });
    let color = "text-warning";
    let isConnection = this.state.compare.IsConnection;
    if(this.state.buy.Exchange === "coinex" || this.state.sell.Exchange === "coinex" ){
      color = "text-warning";
      isConnection = null;
    }
    else if(this.state.compare.IsConnection === true) color = "text-success";
    else if(this.state.compare.IsConnection === false) color = "text-danger";

    return (
      <>
        <Tr data-bs-toggle="collapse" href={"#collapse" + this.state.compare.Name} role="button" aria-expanded="false" aria-controls="collapseExample">
          <Td className={color}>{this.state.compare.Name}</Td>
          <Td>{this.state.compare.Spread.toFixed(2)}</Td>
          <Td>{this.state.buy.Exchange}</Td>
          <Td>{this.state.sell.Exchange}</Td>
          <Td>{updateTime}</Td>
        </Tr>
        <Tr className="collapse" id={"collapse" + this.state.compare.Name}>
          <Td colSpan ="5">
            <div className="d-flex flex-wrap justify-content-center">
              {this.state.compare.Symbols.map((symbol, index)=>
                <Symbol key={index} symbol={symbol} isConnection={isConnection}/>
              )}
            </div>
          </Td>
        </Tr>
      </>
    );
  }

}
