import React, { Component } from 'react';
import { Compare } from "../components/Compare";
import { Table, Thead, Tbody, Tr, Th } from 'react-super-responsive-table';
import 'react-super-responsive-table/dist/SuperResponsiveTableStyle.css';

export class Symbols extends Component {

  constructor(props) {
    super(props);

    let min = sessionStorage.getItem("min");
    let max = sessionStorage.getItem("max") === null ? "20" : sessionStorage.getItem("max");
    let binance = sessionStorage.getItem("binance");
    let bitfinex = sessionStorage.getItem("bitfinex");
    let bybit = sessionStorage.getItem("bybit");
    let coinex = sessionStorage.getItem("coinex");
    let huobi = sessionStorage.getItem("huobi");
    let kucoin = sessionStorage.getItem("kucoin");

    this.state = { dataTime:"",  value: [], min: Number(min), max: Number(max)
    , binance: !(binance !== null && binance === "false")
    , bitfinex: !(bitfinex !== null && bitfinex === "false")
    , bybit: !(bybit !== null && bybit === "false")
    , coinex: !(coinex !== null && coinex === "false")
    , huobi: !(huobi !== null && huobi === "false")
    , kucoin: !(kucoin !== null && kucoin === "false") };
    this.changeMin = this.changeMin.bind(this);
    this.changeMax = this.changeMax.bind(this);
    this.newSymbol = this.newSymbol.bind(this);
    this.changeBinance = this.changeBinance.bind(this);
    this.changeBitfinex = this.changeBitfinex.bind(this);
    this.changeBybit = this.changeBybit.bind(this);
    this.changeCoinex = this.changeCoinex.bind(this);
    this.changeHuobi = this.changeHuobi.bind(this);
    this.changeKucoin = this.changeKucoin.bind(this);
    this.checkExchange = this.checkExchange.bind(this);
    this.exit = this.exit.bind(this);
  }

  componentDidMount() {
    this.loadData();
  }
  exit() {
    sessionStorage.setItem("accessToken", "");
    sessionStorage.setItem("isLogin", "false");
    window.location.href = "/";
  }
  changeMin(event) {
    this.setState({min: event.target.value});
    sessionStorage.setItem("min", event.target.value);
  }
  changeMax(event) {
    this.setState({max: event.target.value});
    sessionStorage.setItem("max", event.target.value);
  }
  changeBinance(event) {
    this.setState({binance: event.target.checked});
    sessionStorage.setItem("binance", event.target.checked);
  }
  changeBitfinex(event) {
    this.setState({bitfinex: event.target.checked});
    sessionStorage.setItem("bitfinex", event.target.checked);
  }
  changeBybit(event) {
    this.setState({bybit: event.target.checked});
    sessionStorage.setItem("bybit", event.target.checked);
  }
  changeCoinex(event) {
    this.setState({coinex: event.target.checked});
    sessionStorage.setItem("coinex", event.target.checked);
  }
  changeHuobi(event) {
    this.setState({huobi: event.target.checked});
    sessionStorage.setItem("huobi", event.target.checked);
  }
  changeKucoin(event) {
    this.setState({kucoin: event.target.checked});
    sessionStorage.setItem("kucoin", event.target.checked);
  }

  newSymbol(symbol){
    return {
      Id: symbol.Id
      , Json: JSON.parse(symbol.Json)
    };
  }

  checkExchange(symbols){
    let buy = symbols.find(symbol => symbol.IsBuy === true);
    if(buy.Exchange === "binance" && this.state.binance) return true;
    else if(buy.Exchange === "bitfinex" && this.state.bitfinex) return true;
    else if(buy.Exchange === "bybit" && this.state.bybit) return true;
    else if(buy.Exchange === "coinex" && this.state.coinex) return true;
    else if(buy.Exchange === "huobi" && this.state.huobi) return true;
    else if(buy.Exchange === "kucoin" && this.state.kucoin) return true;
    else return false;
  }
  
  static renderStatisticsTable(array) {
    return (
      <Table className='table table-dark table-hover text-center align-middle mt-3'>
          <Thead>
              <Tr>
                  <Th className="bg-gradient" style={{width:"20%"}}>Name</Th>
                  <Th className="bg-gradient" style={{width:"20%"}}>Spread</Th>
                  <Th className="bg-gradient" style={{width:"20%"}}>Buy</Th>
                  <Th className="bg-gradient" style={{width:"20%"}}>Sell</Th>
                  <Th className="bg-gradient" style={{width:"20%"}}>Update time</Th>
              </Tr>
          </Thead>
          <Tbody>
              {array.map((compare, index) =>
                <Compare key={compare.Id} compare={compare.Json}/>
              )}
          </Tbody>
      </Table>
    );
  }

  render() {
    return (
      <div className='container mt-3'>
        <div className='d-flex flex-column'>
          <div className='d-flex justify-content-end'>
            <button type="button" className="btn btn-outline-danger" onClick={this.exit}>Log Out</button>
          </div>
          <div className='d-flex justify-content-center'>
            <div className="input-group input-group-sm m-3 w-auto">
              <span className="input-group-text bg-gradient bg-dark text-white">Min:</span>
              <input type="number" className="form-control" value={this.state.min} onChange={this.changeMin}/>
            </div>
            <div className="input-group input-group-sm m-3 w-auto">
              <span className="input-group-text bg-gradient bg-dark text-white">Max:</span>
              <input type="number" className="form-control" value={this.state.max} onChange={this.changeMax}/>
            </div>
          </div>
          <div className='d-flex flex-wrap justify-content-center'>
            
            <div className='m-3'>
              <input type="checkbox" className="btn-check" id="btn-check-outlined-binance" checked={this.state.binance} onChange={this.changeBinance}/>
              <label className="btn btn-outline-secondary" htmlFor="btn-check-outlined-binance">Binance</label>
            </div>
            <div className='m-3'>
              <input type="checkbox" className="btn-check" id="btn-check-outlined-bitfinex" checked={this.state.bitfinex} onChange={this.changeBitfinex}/>
              <label className="btn btn-outline-secondary" htmlFor="btn-check-outlined-bitfinex">Bitfinex</label>
            </div>
            <div className='m-3'>
              <input type="checkbox" className="btn-check" id="btn-check-outlined-bybit" checked={this.state.bybit} onChange={this.changeBybit}/>
              <label className="btn btn-outline-secondary" htmlFor="btn-check-outlined-bybit">Bybit</label>
            </div>
            <div className='m-3'>
              <input type="checkbox" className="btn-check" id="btn-check-outlined-coinex" checked={this.state.coinex} onChange={this.changeCoinex}/>
              <label className="btn btn-outline-secondary" htmlFor="btn-check-outlined-coinex">CoinEx</label>
            </div>
            <div className='m-3'>
              <input type="checkbox" className="btn-check" id="btn-check-outlined-huobi" checked={this.state.huobi} onChange={this.changeHuobi}/>
              <label className="btn btn-outline-secondary" htmlFor="btn-check-outlined-huobi">Huobi</label>
            </div>
            <div className='m-3'>
              <input type="checkbox" className="btn-check" id="btn-check-outlined-kucoin" checked={this.state.kucoin} onChange={this.changeKucoin}/>
              <label className="btn btn-outline-secondary" htmlFor="btn-check-outlined-kucoin">Kucoin</label>
            </div>

          </div>
        </div>
        {Symbols.renderStatisticsTable(this.state.value)}
      </div>
    );
  }



  async loadData() {
    let token = sessionStorage.getItem("accessToken");
    let socket = await new WebSocket(`wss://spreadhunter-001-site1.ctempurl.com/api/WebSockets/ws`, ["client", token]);
    // let socket = new WebSocket(`wss://localhost:7135/api/WebSockets/ws`, ["client", token]);
    let i = 0;
    socket.addEventListener('message', (event) => {
      let obj = JSON.parse(event.data);
      let array = obj.map(p=> this.newSymbol(p));
      let symbols = array.filter(symbol=> symbol.Json.Spread > this.state.min && symbol.Json.Spread < this.state.max && this.checkExchange(symbol.Json.Symbols));
      this.setState({ value: symbols});
      i++;
      if(i % 30 === 0){
        socket.send("Hello!");
      }
    });
    socket.onclose=function(e){
      console.log("WebSocket Error: " , e);
      sessionStorage.setItem("accessToken", "");
      sessionStorage.setItem("isLogin", "false");
      window.location.href = "/";
    };
  }
}
